@import url('https://fonts.googleapis.com/css2?family=Caveat&display=swap');

* {
  font-family: 'caveat', cursive;
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-size: 1.2rem;
}

body {
  background: #fff;
  height: 100vh;
}

header {
  padding: 20px 0;
  color: #ececec;
}

.header-title {
  font-weight: 600;
}

.container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  max-width: 600px;
  margin: 0 auto;
}

.inner {
  width: 100%;
  padding: 8rem 10px 4rem;
}

.form-container {
  width: 100%;
  margin-bottom: 20px;
  display: flex;
  border-radius: calc(0.5 * 100px);
  box-shadow: 0 1px 6px 0 rgba(0, 0, 0, 0.38);
  justify-content: space-evenly;
}

.input-text {
  font-size: 1rem;
  font-weight: 400;
  width: 85%;
  padding-right: 5px;
  padding-left: 10px;
  border-radius: calc(0.5 * 100px);
  cursor: url('./pencil-svgrepo-com.svg'), auto;
}

::placeholder {
  color: #e2e0e0;
}

.input-submit {
  background: transparent;
  color: #5b5b5b;
  text-transform: capitalize;
  cursor: pointer;
  font-weight: 600;
  margin-right: 10px;
}

.input-text,
.input-submit {
  height: 45px;
  outline: none;
  border: none;
}
